<template>
    <div>
        <ConsoleHeader />
      
        <div class="content-wrapper">
            <!-- Content Header (Page header) -->
            <div class="content-header">
                <div class="container-fluid">
                    <div class="row mb-2">
                    <div class="col-sm-6 text-start">
                        <h1 class="m-0 text-dark">Offices<sup><a href="javascript:void(0)" class="text-dark" @click.prevent="newOfficeView" title="Create"><i class="mdi mdi-plus-circle"></i></a></sup></h1>
                    </div><!-- /.col -->
                    <div class="col-sm-6">
                        <ol class="breadcrumb float-sm-right">
                        <li class="breadcrumb-item"><router-link to="/console" >Home</router-link></li>
                        <li class="breadcrumb-item active">Offices</li>
                        </ol>
                    </div><!-- /.col -->
                    </div><!-- /.row -->
                </div><!-- /.container-fluid -->
            </div>
            <!-- /.content-header -->

            <!-- Main content -->
            <section class="content">
                <div class="container-fluid" >
                    
                    <div class="pb-5" v-if="NewOfficeView === false">
                        
                        <div v-if="offices.length < 1" class="text-start">
                            <b>No Offices added yet!</b>
                        </div>

                        <div class="row no-gutters pb-5" v-else >
                            <div class="col-xl-3 col-sm-6 col-6 h-100" v-for="office in offices" v-bind:key="office.id">
                                <!-- small box -->
                                <div class="small-box bg-dark h-100 pt-4">
                                    <div class="inner">
                                        
                                        <h5 class="text-truncate">{{office.post}}</h5>
                                    </div>
                                    <div class="icon">
                                        <!-- <img :src="office.image" class="card-img" style="width: 2em;"/> -->
                                    </div>
                                    <div class="small-box-footer mb-0 p-2">
                                        <a href="javascript:void(0)" class="text-light" @click="filterOffice(office.id)" >More info <i class="fas fa-arrow-circle-right"></i></a>
                                        <a href="javascript:void(0)" class="p-2 text-danger" @click="handleDeleteOffice(office.id)">Delete</a>
                                    </div>
                                </div>
                                

                            </div>
                        </div>


                    </div>

                    <!-- Create Programme -->
                    <div v-else >
                        <div v-if="Office === null">
                            <form method="POST" @submit.prevent="handleNewOffice">
                                <h3 class="text-start">
                                    <a href="javascript:void(0)" class="text-dark" @click="loadOfficeView()"><i class="mdi mdi-chevron-left"></i></a> 
                                    Create a new office
                                </h3>
                                <div class="pb-5">
                
                                    <div class="col-xl-6 col-md-12 my-4">
                            
                                        <div class="form-group text-start">
                                            <label for="">Post</label>
                                            <input type="text" v-model="form.post" class="form-control remove-radius" placeholder="Enter post" required/>
                                        </div>
                                            
                                        <div class="form-group text-start">
                                            <label for="">Slug</label>
                                            <input type="text" v-model="form.slug" class="form-control remove-radius" placeholder="Enter slug" required />
                                        </div>
                
                                    </div>

                                    <div class="col-xl-6 col-md-12 mb-2 text-start">
                                        <button class="btn btn-success" type="submit"><i class="fa fa-save"></i> Save</button>
                                    </div>

                                </div>

                            </form>
                        </div>

                        <div v-else >
                            <form method="POST" @submit.prevent="handleUpdatedOffice">
                                <h3 class="text-start">
                                    <a href="javascript:void(0)" class="text-dark" @click="loadOfficeView()"><i class="mdi mdi-chevron-left"></i></a> 
                                    Update office
                                </h3>
                                <div class="pb-5">
                
                                    <div class="col-xl-6 col-md-12 my-4">
                                            
                                        <div class="form-group text-start">
                                            <label for="">Name</label>
                                            <input type="text" v-model="Office.post" class="form-control remove-radius" placeholder="Enter name" required/>
                                        </div>
                                          
                                        <div class="form-group text-start">
                                            <label for="">Slug</label>
                                            <input type="text" v-model="Office.slug" class="form-control remove-radius" placeholder="Enter venue" required/>
                                        </div>
                
                                    </div>

                                    <div class="col-xl-6 col-md-12 mb-2 text-start">
                                        <button class="btn btn-success" type="submit"><i class="fa fa-save"></i> Save</button>
                                    </div>

                                </div>

                            </form>
                        </div>

                    </div>

                </div>

            </section>
            <!-- /.content -->
        </div>

        <ConsoleFooter />
    </div>
</template>


<script>
import ConsoleHeader from "./layouts/ConsoleHeader.vue";
import ConsoleFooter from "./layouts/ConsoleFooter.vue";

import { mapGetters, mapActions } from "vuex";

export default {
    name: "ConsoleOffice",
    data(){
        return {
            NewOfficeView: false,
            Office: null,
            form: {
                post: '',
                slug: ''
            } 
        }
    },
    computed: mapGetters(['offices']),
    components: {
        ConsoleHeader,
        ConsoleFooter
    },
    methods: {
        ...mapActions(['fetchOffices', 'deleteOffice', 'createOffice', 'updateOffice']),
        async handleNewOffice(){
            await this.createOffice(this.form);
            this.NewOfficeView = false;
            this.form.post = '';
            this.form.slug = '';
            
        },
        async newOfficeView(){
            this.NewOfficeView = true;
            this.Office = null;
        },
        async loadOfficeView(){
            this.NewOfficeView = false
            this.Office = null;
        },
        async filterOffice(id){
            this.Office = this.offices.filter(office => office.id === id);
            if (this.Office.length > 0) {
                this.Office = this.Office[0];
                this.NewOfficeView = null;
            }
        },
        async handleDeleteOffice(id){
            let office = this.offices.filter(office => office.id === id);
            if (office.length > 0) {
                await this.deleteOffice(id);
            }
        },
        async handleUpdatedOffice(){
            if (this.Office !== null) {
                await this.updateOffice({id: this.Office.id, office: this.Office});
                this.loadOfficeView();
                this.form.post = '';
                this.form.slug = '';
            }
        }
    },
    created(){
        this.fetchOffices();
    }

}
</script>

<style scoped>
    .small-box{
        margin-right: 1em;
    }
</style>